.container {
    padding-top: 10vh;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    object-fit: contain;
}

.container > input {
    border-color: lightgray;
    height: 24px;
    width: 200px;
}

.container > button {
    margin: 10px;
}

.textFieldForm {
    margin: 15px;
}

.inputLabel {
    margin: 15px 0 5px 0;
}

.upload-container {
    margin: 30px 0 0;
}

.image-uploaded{
    width: 100%;
}