.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.home,
.share,
.photos,
.deeplink,
.clipboard {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.camera {
  position: relative;
  height: 300px;
  width: 300px;
}

.result {
  position: fixed;
  top: 0;
  right: 100%;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  background-color: rgba(0,0,0,0.9);
  transition: 0.4s;
}

.result.hasPhoto {
  top: 0;
  right: 0;
}

video {
  width: 100%;
  max-width: 100%;
  height: auto;
}

button2 {
  position: absolute;
  top: 400px;
  left: 20px;
  appearance: none;
  border: none;
  outline: none;
  padding: 8px 16px;
  background-image: linear-gradient(to right, #314755 0%, #26a0da  51%, #314755  100%);
  background-position: 0%;
  background-size: 200%;
  color: #FFF;
  font-size: 24px;
  font-weight: 700;
  border-radius: 10px;
  transition: 0.4s;
}

button2:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none; 
}

