.navbar {
    background-color: #060b26;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    background-color: #060b26;
    width: 200px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 700ms;
    z-index: 1;
}

.nav-menu.active {
    left: 0;
    transition: 350;
}

.nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0px 0px 0px 0px;
    list-style: none;
    height: 40px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0px 0px 0px 8px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: #1a83ff;
    width: 80%;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toogle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

span {
    margin-left: 8px;
}

.navbar > h1 {
    color: #fff;
    font-size: 20px;
    padding-left: 20px;
}